.wrapper {
  width: 100%;
  /*@extend .row;*/
  /*composes: row from 'flex';*/
}

.button {
  border: none;
  background: transparent;

  font-family: OpenSans;
  font-size: 15px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.07;
  letter-spacing: normal;

  color: $black-55;
  cursor: pointer;

  padding: 0;

  &.small {
    font-size: 14px;
    line-height: 15px;
  }

  &.big {
    font-size: 15px;
    line-height: 15px;
  }

  &.active {
    color: $water-blue;
  }

  &.disabled {
    color: $black-25;
    cursor: default;
  }
}

.leftButtons {
  /*composes: col-lg-9 from 'flex';*/

  display: flex;
  justify-content: space-between;
}

.rightButtons {
  /*composes: col-lg-3 from 'flex';*/
  display: flex;
  justify-content: flex-end;

  .button {
    padding-left: 25px;
  }
}

.collapsed-buttons {
  display: flex;
  border: none;
  background: transparent;
}
