.defaultWrapper {
  /*@extend .container;*/

  padding: 0;
  margin: 0 auto;

  & > div {
    padding: 0;

    width: 75%;
  }
}

.unstyled {
  font-family: OpenSans;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: normal;
  /*color: $black-85;*/

  margin-bottom: 27px;
}

.header-one {
  display: block;
  font-family: OpenSans;
  font-size: 29px;
  font-weight: 300;
  line-height: 1.17;
  letter-spacing: -0.1px; /* -0.22em*/
  /*color: $black-85;*/

  margin-bottom: 22px;
}

.header-one + .unstyled {
  margin-top: 8px;
}

* + .header-one {
  margin-top: 52px;
}

.header-two {
  display: block;
  font-family: OpenSans;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.25;
  letter-spacing: -0.1px; /* -0.18em*/
  /*color: $charcoal-grey;*/
}

.header-two + .unstyled {
  margin-top: 6px;
}

* + .header-two {
  margin-top: 32px;
}

.image {
  margin-top: 48px;
  margin-bottom: 52px;
}

.quote-one {
  font-family: OpenSans;
  font-size: 16px;
  font-weight: normal;
  font-style: italic;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: normal;
  /*color: $black-85;*/

  position: relative;

  &::before {
    width: 2px;
    height: 100%;
    top: 0;
    left: -22px;
    content: '';
    position: absolute;
    /*background-color: $dark-sky-blue;*/
  }
}

.quote-two {
  font-family: OpenSans-Light;
  font-size: 30px;
  font-weight: 300;
  font-style: italic;
  font-stretch: normal;
  line-height: 1.37;
  letter-spacing: normal;
  /*color: $dark-sky-blue;*/
}

.ordered-list-item, .unordered-list-item {
  margin-bottom: 14px;
}

:global(.public-DraftStyleDefault-ul) + .header-two {
  margin-top: 32px;
}

.quote-two + *:not(.quote-two) {
  margin-top: 54px;
}

*:not(.quote-two) + .quote-two {
  margin-top: 50px;
}

.quote-one + .unstyled {
  margin-top: 27px;
}

.unstyled + .quote-one {
  margin-top: 27px;
}

* + .link {
  margin-top: 37px;
}

.link + * {
  margin-top: 42px;
}
