.container {
  padding: 10px;
  border: 1px solid black;
  margin: 0 auto;

}

.inner {
  width: 560px;

  padding: 0;

  position: relative;

  &::after {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    z-index: 2;
    position: absolute;
  }
}

.embeddedContainer {
  width: 100%;
  padding-bottom: 57%;
  box-sizing: border-box;
  position: relative;
}

.embedded {
  width: 100%;
  height: 100%;

  top: -2px;
  left: -2px;
  z-index: 1;
  position: absolute;

  border: 2px solid transparent;
  box-sizing: border-box;

  &.active {
    border: solid 2px #1998ff;
  }
}
