.wrapper {
  /*@extend .container;*/
  width: 100%;
  display: flex;

  margin: 0 auto;

  .container {
   /* @extend .col-lg-9;*/

    width: 75%;
    display: flex;
    height: 180px;
    border-radius: 3px;
    background-color: #ffffff;
    box-shadow: 2px 2px 8px 0 rgba(0, 0, 0, 0.04);
    border: solid 1px #f3f3f3;
  }
}

.meta-container {
  flex-grow: 2;
  padding: 30px 20px 20px 24px;
  box-sizing: border-box;
}

.title {
  font-family: OpenSans;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.88;
  letter-spacing: normal;
  /*color: $black-85;*/
  margin-bottom: 6px;
}

.description {
  font-family: OpenSans;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  /*color: $black-55;*/
  margin-bottom: 34px;
}

.url {
  font-family: OpenSans;
  font-size: 13px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  /*color: $black-35;*/
}

.image {
  height: 100px;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
