.wrapper {
  width: 100%;
  display: flex;
  flex: 0 1 auto;
  flex-wrap: wrap;
  justify-content: space-around;
}

.button {
  border: none;
  background: transparent;

  font-family: OpenSans;
  font-size: 15px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.07;
  letter-spacing: normal;

  /*color: $black-55;*/
  cursor: pointer;

  padding: 0;

  display: flex;
  align-content: center;
  justify-content: center;

  &-small {
    /*@extend .button;*/

    font-size: 14px;
    line-height: 15px;
  }

  &-big {
    /*@extend .button;*/

    font-size: 15px;
    line-height: 15px;
  }

  .disabled {

  }
}

.leftButtons {
  width: 75%;
  display: flex;
  justify-content: space-between;
}

.rightButtons {
  width: 25%;
  display: flex;
  justify-content: space-around;

  .button {
    padding-left: 25px;
  }
}

.collapsedButtons {
  display: flex;
}

.fileInput {
  display: none;
}
