.wrapper {
  width: 100%;
  /*@extend .row;*/
}

.input {
  width: 100%;
  border: none;
  background: transparent;

  font-family: OpenSans;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.07;
  letter-spacing: normal;

  /*color: $black-90;*/

  &::placeholder {
    /*color: $black-25;*/
  }
}

.button {
  border: none;
  background: transparent;

  font-family: OpenSans;
  font-size: 15px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.07;
  letter-spacing: normal;

  /*color: $black-55;*/
  cursor: pointer;

  padding: 0;

  &.active {
    /*color: $water-blue;*/
  }
}

.leftButtons {
  width: 50%;

  display: flex;
  justify-content: flex-start;
}

.centerButtons {
  width: 25%;

  display: flex;
  justify-content: flex-start;

  & > .button {
    padding-right: 30px;
  }
}

.rightButtons {
  width: 25%;

  display: flex;
  justify-content: flex-end;

  .button {
    padding-left: 25px;
  }
}

.collapsed-buttons {
  display: flex;
}

.file-input {
  display: none;
}
